import React from 'react';
import "./Details.css";
import Png from "./image2.png";
import {MDBIcon} from 'mdb-react-ui-kit';
import AOS from 'aos';
import 'aos/dist/aos.css';

export default function Details() {
    return (
      <>

<div className="d-flex  justify-content-center">
          <h2 style={{color:'black', fontWeight:700}} class="hover-underline-animation">
          <lord-icon
    src="https://cdn.lordicon.com/oegrrprk.json"
    trigger="loop-on-hover"
    delay="10"
    colors="primary:#121331,secondary:#08a88a"
    state="hover"
    style={{width:55,height:55}}>
</lord-icon>
            Some important facts</h2>    
</div>
<div className="d-flex  justify-content-center para1">
          <p>o since the 1500s,
             when an unknown printer took a galley of type and scrambled it to make a type</p>   
</div>

<div className='d-flex justify-content-around details'>
  <div className='para ' data-aos="flip-left"  data-aos-delay="100">
  <MDBIcon fas icon="user-graduate"  className="margin8 margins"  />
    <h3 className='margins'>
      1200+</h3>
    <p className='margins'>Student's</p>
    </div>
    <img src={Png} className="imgpng"/>
  <div className='para' data-aos="flip-left"  data-aos-delay="100">
  <MDBIcon fas icon="chalkboard-teacher"  className="margin8"  />
    <h3>
      400+</h3>
    <p>Teacher's</p></div>
    <img src={Png} className="imgpng"/>
  <div className='para' data-aos="flip-left"  data-aos-delay="100">
  <MDBIcon fas icon="building"  className="margin8"  />
  <h3 >
    100+</h3>
    <p>Class</p>
  </div>
  <img src={Png} className="imgpng"/>
  <div className='para' data-aos="flip-left"  data-aos-delay="100"  >
  <MDBIcon fas icon="bus-alt"  className="margin8"  />
  <h3>
    50+</h3>
    <p>Bus</p>
  </div>
</div>
    </>
    );
  }
  AOS.init();