import React from "react";
import {Navbar, Nav, NavDropdown, Container } from 'react-bootstrap';

import './container.css';
import Logo from './LOGOS.png';
import Line from "./image2.png";

export default function Containers()
{
    return(<>
<div className="bannerImage">

<div className='d-flex justify-content-center'>
<div className=" flex-column mb-3"> 
            <div className="p-2 Texts animate__animated animate__flash animate__delay-1s animate__fast">
            <img src={Logo} />
            </div>        
            <div className="p-2 Texts">
            <img src={Line} className="logo"/>
            </div> 
        <div className="p-2 Texts" >
            <h2  className="animate__animated animate__backInLeft   
            animate__delay-1s animate__slow">Welocome to </h2>
        </div>

        <h1 className="name animate__animated animate__backInRight  
            animate__delay-1s animate__slow" >SVP College</h1>
      </div>
</div>


</div>
        
    
           
    

   

    </>
    )
}